import React from 'react';
import PropTypes from 'prop-types';

export const DateTime = ({ date = "test", ...props}) => {
  if (!date) return null;
  const [dayOfWeek, month, day, year] = new Date(date).toString().split(' ');
  return (
    <time dateline={date} {...props}>
      {month} {day}, {year}
    </time>
  );
};

DateTime.propTypes = {
  date: PropTypes.string,
};
