import React from 'react';
import PropTypes from 'prop-types';

// Components
import { Container } from '../../components';
import {
  StyledHero,
  StyleHeaderWrapper,
} from './styles';

export const Hero = ({
  imagePath,
  title,
  text,
  extraHeight = false,
  ...props
}) => {
  return (
    <StyledHero {...{ imagePath, extraHeight }} {...props}>
      <Container as="div">
        <StyleHeaderWrapper>
          <div>
            {title && <h1>{title}</h1>}
            {text && <p>{text}</p>}
          </div>
        </StyleHeaderWrapper>
      </Container>
    </StyledHero>
  );
};

Hero.propTypes = {
  title: PropTypes.string.isRequired,
  text: PropTypes.string,
  imagePath: PropTypes.string,
  extraHeight: PropTypes.bool,
};
