import React from 'react';
import PropTypes from 'prop-types';

// Components
import { StyledNewsPost, StyledFeaturedImage, StyledArticleBody } from './styles';

export const NewsPost = ({ imagePath, title, children, ...props }) => {
  return (
    <StyledNewsPost {...props}>
      <StyledFeaturedImage>
        <img src={imagePath} alt={title} />
      </StyledFeaturedImage>
      <StyledArticleBody>{children}</StyledArticleBody>
    </StyledNewsPost>
  );
};

NewsPost.propTypes = {
  children: PropTypes.any,
};
